

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import WechatPlugin from 'vux/src/plugins/wechat/index.js'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'

import service from "@/uilt/request"
Vue.use(ToastPlugin)

Vue.use(LoadingPlugin)
Vue.use(AlertPlugin)

Vue.use(WechatPlugin)
Vue.use(AjaxPlugin)
var list = ["onMenuShareTimeline", "onMenuShareAppMessage", "onMenuShareQQ", "onMenuShareWeibo", "onMenuShareQZone",
"chooseImage", "getNetworkType", "hideOptionMenu", "showOptionMenu", "hideMenuItems", "uploadImage",
"showMenuItems", "hideAllNonBaseMenuItem", "showAllNonBaseMenuItem", "closeWindow"];
Vue.http.get(process.env.BASE_API+"apiticket?url=" + window.location.href, (signature) => {
console.log(signature);
Vue.wechat.config({
debug: false,
appId: "wx6a2e25ee7abfca60",
timestamp: "1414587457",
nonceStr: "Wm3WZYTPz0wzccnW",
signature: signature,
jsApiList: list
});
})

Vue.use(VueRouter)

const routes = [{
path: '/',
component: () => import("./view/test/index.vue")
}, {
path: '/carlist',
component: () => import("./view/test/CarList")
}
]


const router = new VueRouter({
routes
})

FastClick.attach(document.body)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
router,
render: h => h(App)
}).$mount('#app-box')



